.search-container{
    background: rgba(255, 255, 255, 0.753);
    height: 30px;
    border-radius: 30px;
    padding: 10px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: 0.8s;
    /*box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
    inset -7px -7px 10px 0px rgba(0,0,0,.1),
   7px 7px 20px 0px rgba(0,0,0,.1),
   4px 4px 5px 0px rgba(0,0,0,.1);
   text-shadow:  0px 0px 6px rgba(255,255,255,.3),
              -4px -4px 6px rgba(116, 125, 136, .2);
  text-shadow: 2px 2px 3px rgba(255,255,255,0.5);*/
  box-shadow:  4px 4px 6px 0 rgba(255,255,255,.3),
              -4px -4px 6px 0 rgba(116, 125, 136, .2), 
    inset -4px -4px 6px 0 rgba(255,255,255,.2),
    inset 4px 4px 6px 0 rgba(0, 0, 0, .2);
    
}


.search-container:hover > .search-input{
    width: 400px;
}

.search-container .search-input{
    background: transparent;
    border: none;
    outline:none;
    width: 0px;
    font-weight: 500;
    font-size: 16px;
    transition: 0.8s;
    color: black;
}
.search-container .search-btn .fas{
    color: #5cbdbb;
}

.search-container:hover{
    -webkit-animation: hoverShake 0.15s linear 3;
            animation: hoverShake 0.15s linear 3;
  }


@-webkit-keyframes hoverShake {
    0% {-webkit-transform: skew(0deg,0deg);transform: skew(0deg,0deg);}
    25% {-webkit-transform: skew(1deg, 1deg);transform: skew(1deg, 1deg);}
    75% {-webkit-transform: skew(-1deg, -1deg);transform: skew(-1deg, -1deg);}
    100% {-webkit-transform: skew(0deg,0deg);transform: skew(0deg,0deg);}
}


@keyframes hoverShake {
    0% {-webkit-transform: skew(0deg,0deg);transform: skew(0deg,0deg);}
    25% {-webkit-transform: skew(1deg, 1deg);transform: skew(1deg, 1deg);}
    75% {-webkit-transform: skew(-1deg, -1deg);transform: skew(-1deg, -1deg);}
    100% {-webkit-transform: skew(0deg,0deg);transform: skew(0deg,0deg);}
}



.containerBuscador{
    max-width: 900px;
    margin: 0 auto;
}

.cnt {
    padding-top: 10px;
}

.form-container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.form-container button {
    margin-left: 20px;
}

.label {
    padding-right: 10px;
}



.formBuscador {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2%;
    gap: 2%;
    transition: all 0.5s;
    max-width: 820px;
    margin: 5% auto;
}

.formBuscador input {
    background-color: rgba(255, 255, 255, 0);
    color: #fff;
    border-top: 0px;
    border-left: 0px;
    border-right: 0px;
    border-bottom: 1px solid #fff;
    width: 720px;
}

.formBuscador::-webkit-input-placeholder {
    color: #fff;
}

.formBuscador:-ms-input-placeholder {
    color: #fff;
}

.formBuscador::placeholder {
    color: #fff;
}

.formBuscador input:focus {
    height: 95%;
    font-size: 15px;
    border-radius: 15px;
    padding-left: 10px;
}

.submitBtn {
    border-radius: 15px;
    margin-right: 5px;
    background: transparent;
    border: 0 solid transparent;
}

.submitBtn:hover {
    cursor: pointer;
}


.movieCard {
    position: relative;
    display: block;
    width: 800px;
    height: 350px;
    margin: 100px auto;
    overflow: hidden;
    border-radius: 10px;
    transition: all 0.4s;
    box-shadow: 2px 2px 2px 2px;
    padding-bottom: 25px;  
}

.movieCard:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    transition: all 0.4s;
    box-shadow: 5px 5px 5px 5px;
}

.fav {
    position: absolute;
    top: 0;
    right: 0;
    height: 10%;
    padding-left: 12px;
    padding-right: 12px;
    padding-bottom: 20px;
    display: inline-block;
    background-color: rgba(255, 255, 255, 0.178);
    border: 1px solid rgba(255, 255, 255, 0.164);
    color: black;
    transition: color 0.3s;
    transition-delay: 0.15s;
    margin: 0 10px;
    border-radius: 15px;
    transition: .5s;
}

.fav:hover {
    cursor: pointer;
    padding-left: 10px;
    padding-right: 10px;
}

.start {
    transition: .5s;
}

.start:hover {
    margin: 0 auto;
    padding-left: 10px;
    padding-right: 10px;
}

.info_section {
    position: relative;
    width: 100%;
    height: 100%;
    background-blend-mode: multiply;
    z-index: 2;
    border-radius: 10px;
}

.movie_header {
    position: relative;
    padding: 25px;
    height: 40%;
}

.movie_header h1 {
    color: rgb(192, 192, 192);
    font-weight: 400;
}

.movie_header a {
    text-decoration: none;
}

.type {
    display: inline-block;
    margin-top: 10px;
    color: rgb(192, 192, 192);
    padding: 5px;
    border-radius: 5px;
    border: 1px solid rgba(255, 255, 255, 0.13);
}

.year {
    display: inline-block;
    color: rgb(192, 192, 192);
    margin-left: 10px;
}

.locandina {
    position: relative;
    float: left;
    margin-right: 20px;
    height: 290px;
    box-shadow: 0 0 20px -10px rgba(0, 0, 0, 0.5);
}

.movie_desc {
    height: 50%;
    padding: 25px;  
}

.text {
    color: #cfd6e1;
}
    
.navbar {
    height: 56px;
